@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
}
body{
    max-width: 1440px;
}

main{
    height: 100vh;
    max-width: 100vw;
    background-color: hsl(226, 43%, 10%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    height: 480px;
    width: 1024px;
    display: grid;
    gap: 15px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}
.gridrowspan2{
    grid-row: span 2;
}
.box{
    border-radius: 12px;
    overflow: hidden;
    color: white;
}
.box1{
    background-color: hsl(235, 46%, 20%);
    color: hsl(0, 0%, 100%);
    font-weight: 300;
}
.upper{
    padding: 1.5em;
    height: 70%;
    background-color: hsl(246, 80%, 60%);
    border-radius: 12px;
}
.upper img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
    border: 4px solid white;
    margin-bottom: 1.3em;
}
.upper p{
    font-size: 14px;
    font-weight: 400;
    color: hsl(236, 100%, 87%);
    margin-bottom: .2em;
}
.upper h1{
    font-size: 2rem;
    font-weight: 300;
    padding: 0 0 1em;
}
.box1 ul{
    list-style-type: none;
    padding: 0.8em 1.5em;
    display: flex;
    flex-direction: column;
    gap: 8px;
    color: #bdc1ff;
}
.box1 ul li{
    padding: 5px 0;
}
.box1 ul li:hover{
    color: white;
    cursor: pointer;
}
.imgcontainer{
    height: 84px;
}
.box2 .imgcontainer{
    background-color: hsl(15, 100%, 70%);
}

.imgcontainer svg{
    position: relative;
    top: -10px;
    left: 60%;
}
.box5 .imgcontainer svg{
    position: relative;
    top: 0px;
}
.textcard{
    background-color: hsl(235, 46%, 20%);
    height: 80%;
    border-radius: 12px;
    position: relative;
    top: -15%;
    padding: 1.5em;
    cursor: pointer;
}
.textcard:hover{
    background-color: hsl(237, 33%, 26%);
}
.heading, .timings{
    display: flex;
    justify-content: space-between;
}.heading{
    align-items: center;
}
.dots{
    cursor: pointer;
}
.dots :hover{
    fill: white;
}
.timings{
    flex-direction: column;
}
.timings h2{
    font-size: 2.3rem;
    font-weight: 300;
    padding: 20px 0 10px;
}
h2 span{
    font-size: 2.3rem;
    font-weight: 300;
    padding: 20px 0 10px;
}
.timings p{
    font-size: 14px;
    font-weight: 400;
    color: hsl(236, 100%, 87%);
}

.box3 .imgcontainer{
    background-color: hsl(195, 74%, 62%);
}

.box4 .imgcontainer{
    background-color: hsl(348, 100%, 68%);
}

.box5 .imgcontainer{
    background-color: hsl(145, 58%, 55%);
}

.box6 .imgcontainer{
    background-color: hsl(264, 64%, 52%);
}

.box7 .imgcontainer{
    background-color: hsl(43, 84%, 65%);
}
.active{
    color: white;
}

@media screen and (max-width: 700px) {
    main{
        height: auto;
        padding: 5em 0;
    }
    .container{
        height: auto;
        width: 95%;
        grid-template-columns: 1fr;
    }
    .box1 ul{
        padding: 0 1.5em;
        margin-top: 1em;
        flex-direction: row;
        justify-content: space-between;
    }
    .textcard{
        padding-bottom: 0;
    }
    .imgcontainer svg{
        left: 75%;
    }
    .timings{
        flex-direction: row;
        align-items: baseline;
    }
}